import React, { Component } from 'react'
import { Table,Popconfirm, Button } from 'antd';
import 'antd/dist/antd.css';
import { Request } from '../config/utils';
import { Url } from '../config/config';
import Error from './ErrorPage';

function onChange(pagination, filters, sorter) {
  console.log('params', pagination, filters, sorter);
}

export default class blogEdit extends Component {
    constructor(props) {
        super(props);

        this.columns = [{
            title: 'ID',
            dataIndex: 'id',
            sorter: (a, b) => a.id - b.id,
            // specify the condition of filtering result
            // here is that finding the name started with `value`
            }, {
                title: 'Date',
                dataIndex: 'date',
                defaultSortOrder: 'descend',
                sorter: (a, b) => a.date.localeCompare(b.date),
            }, {
            title: 'Title',
            dataIndex: 'title',
            key: 'title',
            render: (text, articles) => 
                <a href={articles.url}>{text}</a>,
            }, {
            title: 'Writer',
            dataIndex: 'writer',
            }, {
                title: 'Action',
                dataIndex: 'action',
                render: (text, record) => (
                    this.state.articles.length >= 1 ? (
                        <div>
                            <Button onClick={() => this.handleReviseClick(record)}>Revise</Button>
                            <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.id)}>
                                <Button type="danger">Delete</Button>
                            </Popconfirm>
                        </div>
                    ) : null
                )
            },
        ];
    
        this.state = {
            articles: [],
            current: 1,
            pageSize: 10,
            total: 0,
            error: null,
            isLoaded: false,
        }
    }

    componentDidMount() {
        this.gotoThispage(this.state.current);
    }

    gotoThispage(current) {
        const body = {
            current: current,
            pagesize: this.state.pageSize,
        }
        console.log(body);
        Request.getCurrentListPage({
            current: current,
            pagesize: this.state.pageSize,
        }).then(
            res => {
                console.log("res", res.articles);
                //if(Request.judgeRes(res) === true){
                    this.setState({
                        current: current,
                        articles: res.data.articles,
                        total: res.data.total,
                        isLoaded: true,
                    });
                //}
            }
        ).catch(
            error => this.setState({
                isLoaded: true,
                error
            })
        )
    }

    /* 添加博文 */
    handleAddClick = () =>  {
        console.log("add blog");
        this.props.router.push(Url.blogAddPage);
    }

    /* 修改博文 */
    handleReviseClick = (value) => {
        console.log(value);
        this.props.router.push({
            pathname: Url.blogRevisePage,
            query: {
                billId: value.id,
            }
        });
    }

    /* 删除Record */
    handleDelete = (key) => {
        console.log(key);
        const articles = [...this.state.articles];
        Request.deleteBlog({id: key}).then(
            res => {
                if(Request.judgeRes(res) === true){
                    this.setState({ articles: articles.filter(item => item.id !== key)});
                    window.alert("delete success");
                    this.gotoThispage(this.state.current);
                }
            }
        )
    }

    render() {
        const self = this;
        const columns = this.columns;
        const { error, isLoaded, articles } = this.state;
        if (error) {
            return <Error component={error} />
        }
        else if (!isLoaded) {
            return <div>Loading...</div>
        }
        else{
            return (
                <div>
                    <h2>鹰眼后台管理系统</h2>
                    <Button type="primary" icon="plus" onClick={this.handleAddClick}>增加博文</Button>
                    <Table 
                        columns={columns}
                        dataSource={articles} 
                        rowKey={articles => articles.id}
                        onChange={onChange}
                        pagination={{  //分页
                            total: this.state.total, //数据总数量
                            pageSize: this.state.pageSize,  //显示几条一页
                            onChange(current) {  //点击改变页数的选项时调用函数，current:将要跳转的页数
                                self.gotoThispage(current);
                            },
                            showTotal: function () {  //设置显示一共几条数据
                                return '共 ' + self.state.total + ' 条数据';
                            }
                        }}
                    />
                </div>
            );
        }
    }
}