import React, { Component } from 'react';
import '../style/blogDisplay.css';
import { Request } from '../config/utils';

export default class blogDisplay extends Component {
    constructor() {
        super();
        this.state = {
            id: "",
            title: "",
            date: "",
            writer: "",
            content: "",
        }
    }

    /* 加载文章 */
    componentWillMount() {
        Request.getArticleContent(this.props.location.query.id).then(
            res => {
                this.setState(res.data);
                console.log(this.state)
            }
        )
    }

    render() {
        return (
            <div id="blog">
                <h1 className="blogtitle">{this.state.title}</h1>
                <p className="blogwriter">{this.state.writer}</p>
                <p className="blogdate">{this.state.date}</p>
                <div id="blogtext">
                    <pre>{this.state.content}</pre>
                </div>
            </div>
        );
    }
}