import React, { Component } from 'react'
import { Table, Spin, Icon } from 'antd';
import 'antd/dist/antd.css';
import { Request } from '../config/utils';
import { Url } from '../config/config';
import Error from './ErrorPage';

const columns = [{
  title: 'ID',
  dataIndex: 'id',
  sorter: (a, b) => a.id - b.id,
  // specify the condition of filtering result
  // here is that finding the name started with `value`
}, {
    title: 'Date',
    dataIndex: 'date',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.date.localeCompare(b.date),
}, {
  title: 'Title',
  dataIndex: 'title',
  key: 'title',
  render: (text, articles) => (
      <a href={Url.blogDisplayPage+"?id="+articles.id} >{text}</a>
  )
}, {
  title: 'Writer',
  dataIndex: 'writer',
}];

function onChange(pagination, filters, sorter) {
  console.log('params', pagination, filters, sorter);
}

export default class blogList extends Component {
    constructor() {
        super();
        this.state = {
            articles: [],
            current: 1,
            pageSize: 10,
            total: 0,
            error: null,
            isLoaded: false,
        }
        this.gotoThispage = this.gotoThispage.bind(this);
    }

    componentDidMount() {
        this.gotoThispage(this.state.current, this.state.pageSize);
    }

    gotoThispage(current) {
        const body = {
            current: current,
            pagesize: this.state.pageSize,
        }
        Request.getCurrentListPage(body).then(
            res => {
                console.log("res", res.data);
                this.setState({
                    current: current,
                    articles: res.data.articles,
                    total: res.data.total,
                    isLoaded: true,
                })
            }
        ).catch(
            error => this.setState({
                isLoaded: true,
                error
            })
        )
    }

    render() {
        const self = this;
        const { error, isLoaded, articles } = this.state;
        const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />
        if (error) {
            return <Error component={error} />
        }
        else if (!isLoaded) {
            return (
                <div style={{width:"50px",margin:"200px auto"}}>
                    <Spin indicator={antIcon} tip="Loading" />
                </div>
            );
        }
        else{
            return (
                <div>
                    <Table
                        columns={columns}
                        dataSource={articles}
                        rowKey={articles => articles.id}
                        onChange={onChange}
                        pagination={{  //分页
                            total: this.state.total, //数据总数量
                            pageSize: this.state.pageSize,  //显示几条一页
                            onChange(current) {  //点击改变页数的选项时调用函数，current:将要跳转的页数
                                self.gotoThispage(current);
                            },
                            showTotal: function () {  //设置显示一共几条数据
                                return '共 ' + self.state.total + ' 条数据';
                            }
                        }}
                    >
                    </Table>
                </div>
            );
        }
    }
}