import React, { Component } from 'react'
import { Input } from 'antd/lib';
import { Button, Form } from 'antd';
import { Url } from '../config/config';
import "../style/blogadd.css";
import { getNowFormatDate, Request } from '../config/utils';
//import ReactQuill from 'react-quill';
//import BraftEditor from 'braft-editor';
//import 'braft-editor/dist/index.css';

export default class blogAdd extends Component{
    constructor() {
        super();
        this.state = {
            title: "",
            date: "",
            writer: "",
            content: "",
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        this.setState({date: getNowFormatDate(new Date())}, function(){
            console.log(this.state);
            Request.postNewBlog(this.state).then(
                res => {
                    if(Request.judgeRes(res) === true){
                        window.alert("add blog success");
                        this.props.router.push(Url.adminHomePage);
                    }
                }
            )
        });
    }

    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    render(){
        const { TextArea } = Input;
        return(
            <Form onSubmit={this.handleSubmit} className="edit-Form">
                Title
                <Input name="title" className="edit-Input-Title" onChange={this.handleChange} required />
                <br />
                Content
                <TextArea name="content" className="edit-TextArea" rows={30} value={this.state.content} onChange={this.handleChange} required />
                Writer
                <Input  name="writer" className="edit-Input-Writer"  onChange={this.handleChange} required />
                <Button type="primary" htmlType="submit" className="edit-Button" >
                    Add Blog
                </Button>
            </Form>
            // <BraftEditor
            //     value={this.state.content}
            //     onChange={this.handleChange}
            // />
        )
    }
}