import React, { Component } from 'react';
import { Input } from 'antd/lib';
import { Url } from '../config/config';
import "../style/blogadd.css";
import { getNowFormatDate, Request } from '../config/utils';
import { Button, Form } from 'antd';

export default class blogRevise extends Component {
    constructor() {
        super();
        this.state = {
            id: "",
            title: "",
            date: "",
            writer: "",
            content: "",
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleClick(event) {
        this.setState({date: getNowFormatDate(new Date())}, function(){
            console.log(this.state);
            Request.postReviseBlog(this.state).then(
                res => {
                    if(Request.judgeRes(res) === true){
                        window.alert("Revise success!");
                        this.props.router.push(Url.adminHomePage);
                    }
                }
            )
        });
    }

    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        });
      }

    componentWillMount() {
        const ID = this.props.location.query.billId;
        Request.getArticleContent(ID).then(
            res => {
                if(Request.judgeRes(res) === true){
                    this.setState({
                        id: ID,
                        title: res.data.title,
                        date: res.data.date,
                        writer: res.data.writer,
                        content: res.data.content,
                    });
                }
            }
        )
    }

    render() {
        const { TextArea } = Input;
        return (
            <Form onSubmit={this.handleSubmit} className="edit-Form">
                Title
                <Input name="title" defaultValue={this.state.title} className="edit-Input-Title" onChange={this.handleChange} required/>
                <br />
                Content
                <TextArea name="content" className="edit-TextArea" rows={30} value={this.state.content} onChange={this.handleChange} required>
                </TextArea>
                writer
                <Input  name="writer" defaultValue={this.state.writer} className="edit-Input-Writer" readOnly={true} onChange={this.handleChange} required/>
                <Button type="primary" htmlType="submit" className="edit-Button" onClick={this.handleClick}>
                    submit
                </Button>
            </Form>
        );
    }
}