import React from 'react';
import '../style/login-form.css';
import { Request } from '../config/utils';
import { Form, Icon, Input, Button, Checkbox } from 'antd';
import { Url, StatusCode } from '../config/config';
import Error from './ErrorPage';

const FormItem = Form.Item;

class NormalLoginForm extends React.Component {
  constructor(){
    super();
    this.state = {
      error: null
    };
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        Request.postLogin(values).then(
          res => {
            /*
            if(Request.judgeRes(res) === true){
              this.props.router.push(Url.adminHomePage)
            }*/
            /* 登陆验证用户反馈*/
            console.log(res);
            if(res.data.statusCode === StatusCode.OK){
              console.log("login success");
              this.props.router.push(Url.adminHomePage)
            } else if(res.data.statusCode === StatusCode.PWD_ERROR){
              window.alert("password error!");
            } else if(res.data.statusCode === StatusCode.USR_NOEXIST){
              window.alert("this username isnot exist");
            }
          }
        ).catch(error => 
          this.setState({error})
        )
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { error } = this.state;
    if(error) {
      return <Error component={error}/>
    }
    return (
      <div id="components-form-demo-normal-login">
        <Form onSubmit={this.handleSubmit} className="login-form">
          <FormItem>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Please input your username!' }],
            })(
              <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Username" />
            )}
          </FormItem>
          <FormItem>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Please input your Password!' }],
            })(
              <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
            )}
          </FormItem>
          <FormItem>
            {getFieldDecorator('remember', {
              valuePropName: 'checked',
              initialValue: true,
            })(
              <Checkbox>Remember me</Checkbox>
            )}
            <a className="login-form-forgot" href={Url.forgetPasswordPage}>Forgot password</a>
            <Button type="primary" htmlType="submit" className="login-form-button">
              Log in
            </Button>
            Or <a href={Url.registerPage}>register now!</a>
          </FormItem>
        </Form>
      </div>
    );
  }
}

export const WrappedNormalLoginForm = Form.create()(NormalLoginForm);