import React, { Component } from "react";
import { Layout, Menu} from "antd";
import { Url } from '../config/config';
import '../style/blogFrame.css'
const { Header, Footer } = Layout;

export default class BlogFrame extends Component {
  handleClick = (e) => {
    console.log(e.key);
    this.props.router.push(e.key);
  }

  render() {
    return (
      <div>
        <Layout>
          <Header>
            <div className="logo" >
              <img src={require("../images/logo.png")} alt="logo" width="180px" height="225px" />

            </div>
            <Menu className="Menu"
              theme="dark"
              mode="horizontal"
              defaultSelectedKeys={["1"]}
              style={{ lineHeight: "64px" }}
              onClick={this.handleClick}
            >
              <Menu.Item key={Url.homePage}>首页</Menu.Item>
              <Menu.Item key={Url.introductionPage}>简介</Menu.Item>
              <Menu.Item key={Url.blogListPage}>帖子</Menu.Item>
              <Menu.Item key={Url.loginPage} style={{float:"right"}}>登录</Menu.Item>
            </Menu>
          </Header>
          <Layout style={{minHeight:780}}>
            {this.props.children}
          </Layout>
          {/*footer */}
          <Footer className="footer">
            © YINGYAN-Team
          </Footer>
        </Layout>
      </div>
    );
  }
}
