import React, { Component } from "react";
import { Table, Tag } from "antd";
import 'antd/dist/antd.css';

const columns = [{
    title:'角色',
    dataIndex:'character',
    key:'character'
},{
    title:'年级',
    dataIndex:'tags',
    key:'tags',
    render: tags => (
        <span>
          {tags.map(tag => <Tag color="blue" key={tag}>{tag}</Tag>)}
        </span>
      ),
},{
    title:'人数',
    dataIndex:'number',
    key:'number',
}]

const data = [{
    key:'1',
    character:'领导者',
    tags:['大四'],
    number:'2'
},{
    key:'2',
    character:'主力军',
    tags:['大三'],
    number:'3'
},{
    key:'3',
    character:'新锐',
    tags:['大二'],
    number:'5'
}]

export default class MemberIntro extends Component {
    render(){
        return(
            <div id="container" style={{padding: '24px'}}>
                <Table columns={columns} dataSource={data} />
            </div>
        )
    }
}