import React, { Component } from "react";
import { Row, Col } from "antd";
import 'antd/dist/antd.css';
import '../style/techIntro.css';

export default class TechIntro extends Component{
    render(){
        return(
            <div className="techWrap">
                <h2>技术方向:</h2>
                <Row>
                    <Col span={12}>前端:</Col>
                    <Col span={12}>后台:</Col>
                </Row>
                <Row>
                    <Col span={12}>React</Col>
                    <Col span={12}>django</Col>
                </Row>
                <Row>
                    <Col span={12}>Antd</Col>
                    <Col span={12}>Flask</Col>
                </Row>
            </div>
        )
    }
}