import React, { Component } from "react";
import { Layout } from "antd";
import SiderList from "./blogFrameSiderList";
import '../style/blogFrame.css'
const { Content, Sider } = Layout;

export default class homeContent extends Component {
    render() {
        return (
            <Layout>
                <div>
                <div className="recent"><p>Recent| </p></div>
                <Sider className="sider" >
                    <SiderList />
                </Sider>
                </div>
                <Content className="content">
                    <div id="author">author/date</div>
                    <div style={{ background: "#fff", padding: 24, minHeight: 680 ,color:'black'}}>
                        <p id="contentText">...</p>
                    </div>
                </Content>
            </Layout>
        );
    }
}