import React from 'react';
import { Layout, Menu,Icon } from "antd";
import "../style/introduction.css";
import MemberIntro from './memberIntro';
import TechIntro from './techIntro';
import HopeIntro from './hopeIntro';

const { Content, Sider } = Layout
export default class Introduction extends React.Component {
    constructor(){
        super();
        this.state={
            page:'1',
            Collapsed:false,
            // 1:人员介绍界面,2:技术介绍界面,3:新年愿望界面
        }
        this.renderPage=this.renderPage.bind(this);
    }

    handleClick = (event) => {
        this.setState({
            page:event.key,
        })
    }
    renderPage(){
        console.log(this.state.page);
        switch(this.state.page){
            case '1':return (<MemberIntro/>);
            case '2':return (<TechIntro/>);
            case '3':return (<HopeIntro/>);
            default:return null;
        }
    }
    render() {
        return (
            <Layout>
                <Sider>
                    <Menu
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        mode="inline"
                        theme="dark"
                        inlineCollapsed={this.state.collapsed}
                        style={{width:200}}
                        onClick={this.handleClick}
                    >
                        <Menu.Item key="1">
                            <Icon type="pie-chart" />
                            <span>人员介绍</span>
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Icon type="desktop" />
                            <span>技术方向</span>
                        </Menu.Item>
                        <Menu.Item key="3">
                            <Icon type="inbox" />
                            <span>新年愿望</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Content>
                    {/* {this.props.children} */}
                    {this.renderPage()}
                    {/* {(()=>{
                        switch(this.state.page){
                            case '1':return (<MemberIntro/>);break;
                            case '2':return (<TechIntro/>);break;
                            case '3':return (<HopeIntro/>);break;
                            default:return null;
                        }
                    })()} */}
                </Content>
            </Layout>
                )
            }
}