import React,{ Component } from 'react';
import { Request } from '../config/utils';
import { Form, Input, Tooltip, Icon, Checkbox, Button, Modal} from 'antd';
import { Url, StatusCode } from '../config/config';
import "../style/register-form.css";
import { browserHistory } from 'react-router';
  
const FormItem = Form.Item;

function countDown(time) {
  let secondsToGo = time;
  const modal = Modal.success({
    title: 'You have been a memeber of us!',
    content: `This modal will be destroyed after ${secondsToGo} second.`,
  });
  const timer = setInterval(() => {
    secondsToGo -= 1;
    modal.update({
      content: `This modal will be destroyed after ${secondsToGo} second.`,
    });
  }, 1000);
  setTimeout(() => {
    clearInterval(timer);
    modal.destroy();
    browserHistory.push(Url.homePage);
  }, secondsToGo * 1000);
  return true;
}

class RegistrationForm extends Component {
  constructor(props){
    super(props);
    this.state = {
      confirmDirty: false,
      agreementVisible: false,
    }
  }

  setAgreementVisible(agreementVisible) {
    this.setState({ agreementVisible });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values, callback) => {
      if (!err) {
        if (values.agreement === true) {
          console.log('Received values of form: ', values);
          const data = {
            userName: values.userName,
            password: values.password,
            email: values.email,
          }
          console.log(data);
          Request.postRegister(data).then(
            res => {
              if (res.data.statusCode === StatusCode.OK){
                countDown(5);
              } else {

              }
            }
          )
        } else {
          window.alert("You have not read our agreement!");
        }
      }
    });
  }

  handleConfirmBlur = (e) => {
    const value = e.target.value;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  }

  compareToFirstPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && value !== form.getFieldValue('password')) {
      console.log(value);
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  }

  validateToNextPassword = (rule, value, callback) => {
    const form = this.props.form;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  }

  render() {
    const { getFieldDecorator } = this.props.form;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };

    return (
      <div id="components-form-demo-normal-register">
        <h1>Register</h1>
        <Form onSubmit={this.handleSubmit}>
          <FormItem
            {...formItemLayout}
            label={(
              <span>
                Username&nbsp;
                <Tooltip title="What do you want others to call you?">
                  <Icon type="question-circle-o" />
                </Tooltip>
              </span>
            )}
          >
            {getFieldDecorator('userName', {
              rules: [{ required: true, message: 'Please input your username!', whitespace: true }],
            })(
              <Input />
            )}
          </FormItem>
          <FormItem
            {...formItemLayout}
            label="Password"
          >
            {getFieldDecorator('password', {
              rules: [{
                required: true, message: 'Please input your password!',
              }, {
                validator: this.validateToNextPassword,
              }],
            })(
              <Input type="password" />
            )}
          </FormItem>
          <FormItem
            {...formItemLayout}
            label="Confirm Password"
          >
            {getFieldDecorator('confirm', {
              rules: [{
                required: true, message: 'Please confirm your password!',
              }, {
                validator: this.compareToFirstPassword,
              }],
            })(
              <Input type="password" onBlur={this.handleConfirmBlur} />
            )}
          </FormItem>
          <FormItem
            {...formItemLayout}
            label="E-mail"
          >
            {getFieldDecorator('email', {
              rules: [{
                type: 'email', message: 'The input is not valid E-mail!',
              }, {
                required: true, message: 'Please input your E-mail!',
              }],
            })(
              <Input />
            )}
          </FormItem>
          <FormItem {...tailFormItemLayout}>
            {getFieldDecorator('agreement', {
              valuePropName: 'checked',
            })(
              <Checkbox>I have read the <a onClick={() => this.setAgreementVisible(true)}>agreement</a></Checkbox>
              
            )}
          </FormItem>
          <FormItem {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">Register</Button>
          </FormItem>
        </Form>
        <Modal
          title="Agreement"
          centered
          visible={this.state.agreementVisible}
          onOk={() => this.setAgreementVisible(false)}
          onCancel={() => this.setAgreementVisible(false)}
        >
          <p>some contents...</p>
          <p>some contents...</p>
          <p>some contents...</p>
        </Modal>
      </div>
    );
  }
}
  
export const WrappedRegistrationForm = Form.create()(RegistrationForm);